<template lang="html">
  <div class="help-blocks">
    <template v-if="validation.$error">
      <p class="help-block" v-if="validation.required===false"></p>
      <p class="help-block" v-if="validation.requiredIf===false"></p>
      <p class="help-block" v-if="validation.requiredUnless===false"></p>
      <p class="help-block" v-if="validation.email===false"></p>
      <p class="help-block" v-if="validation.url===false"></p>
      <p class="help-block" v-if="validation.ipAddress===false">IP</p>
      <p class="help-block" v-if="validation.macAddress===false">MAC</p>
      <p class="help-block" v-if="validation.alpha===false"></p>
      <p class="help-block" v-if="validation.numberic===false"></p>
      <p class="help-block" v-if="validation.integer===false"></p>
      <p class="help-block" v-if="validation.decimal===false"></p>
      <p class="help-block" v-if="validation.alphaNum===false"></p>
      <p class="help-block" v-if="validation.minLength===false">{{validation.$params['minLength']['min']}}</p>
      <p class="help-block" v-if="validation.maxLength===false">{{validation.$params['maxLength']['max']}}</p>
      <p class="help-block" v-if="validation.minValue===false">{{validation.$params['minValue']['min']}}</p>
      <p class="help-block" v-if="validation.maxValue===false">{{validation.$params['maxValue']['max']}}</p>
      <p class="help-block" v-if="validation.sameAs===false">{{validation.$params['sameAs']['eq']}}</p>
    </template>
  </div>
</template>

<script>
export default {
  name: 'FormErrorMessage',
  props: {
    validation: {
      type: Object,
      default () {
        return {}
      }
    }
  }
}
</script>

<style lang="css">
.help-blocks {
  display: inline-block;
}
</style>



